import React from "react"
import { RpJsonDocument } from "@rp/react/rpjson/types"
import { useEntitiesStyles } from "@rp/react/web/common/document-viewer"
// Packages
import { RpJsonAnnotationStyles, RpJsonParagraph } from "@rp/react/web/rpjson"
// Styles
import { DetectionCardContainer } from "./Detections.styles"

type Props = {
  rpjsonDocument: RpJsonDocument
  detection: {
    queryType: string
    key: string
    pnum: number
    snum: number
  }
  onClick: (entityId: string) => void
  selected?: boolean
}

const DetectionCard = ({ rpjsonDocument, onClick, detection, selected = false }: Props) => {
  const { key, pnum, snum, queryType } = detection
  const highlights = useEntitiesStyles([key], true)

  return (
    <DetectionCardContainer selected={selected} onClick={() => onClick(key)}>
      <RpJsonAnnotationStyles
        highlights={["entity", "rp_topic"].includes(queryType) ? highlights : []}
      >
        <RpJsonParagraph
          paragraphNumber={pnum}
          rpjson={rpjsonDocument}
          sentenceNumber={snum}
          markupDetections
        />
      </RpJsonAnnotationStyles>
    </DetectionCardContainer>
  )
}

export { DetectionCard }
